.product-cards {
  
  
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}


.cart-items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  
}

.cart-item {
  border: 3px solid black;
  padding: 2px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cart-item img {
  max-width: 100%;
}
.checkout-section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  
  color: aliceblue;
